<template>
<div>
    <div class="row" :style="getStyle">
        <div class="col-md-8 offset-md-1">
            <vueInstant :type="type" :suggestion-attribute="suggestionAttribute" @input="changed" :suggestions="suggestions" v-model="val"></vueInstant>
        </div>
    </div>

    <div class="row">
      <div class="col-md-12">
      <div class="jumbotron jumbotron-fluid" style="background:#202f40;">
        <div class="container">
          <h1 class="display-5" style="color:white;">VUE INSTANT</h1>
          <p class="lead" style="color:white;">vue instant allows you to easily create custom search controls with auto suggestions for your vue 2 application.</p>
          <div>
          <a href="https://www.npmjs.com/package/vue-instant"><img src="https://img.shields.io/npm/dt/vue-instant.svg" alt="downloads"></a>
          <a href="https://www.npmjs.com/package/vue-instant"><img src="https://img.shields.io/npm/v/vue-instant.svg" alt="Version"></a>
