      </div>
    </div>

    <div class="row" style="margin-top:50px;">

        <div class="col-md-8 offset-md-2">
          <h5>Try example styles</h5>
          <small style="background-color:rgba(255, 255, 0, 0.24);">Try typing your favorite film in the searchbox of the top of the page</small>
          <div>
            <el-button @click="changeType('google')"  size="mini" type="danger">Google</el-button>
            <el-button @click="changeType('facebook')"  size="mini" type="primary">Facebook</el-button>
            <el-button @click="changeType('amazon')"  size="mini" type="warning">Amazon</el-button>
            <el-button @click="changeType('spotify')"  size="mini" type="success">Spotify</el-button>
            <el-button @click="changeType('twitter')"  size="mini" type="info">Twitter</el-button>
            <div></div>
            <small><a target="_blank" href="http://shipow.github.io/searchbox/"> Or create your own style in this external page!</a></small>
          </div>
        </div>
    </div>

    <div class="row" style="margin-top:50px;">
        <div class="col-md-8 offset-md-2">
          <h5 style="background-color:rgba(255, 255, 0, 0.24);">Information (current status: development mode)</h5>
          <p>vue instant allows you to easily create custom search controls with auto suggestions for your vue 2 application. You can see multiples examples in this page or clone the repo.</p>
        </div>
    </div>

  <div class="row" style="margin-top:10px;">
      <div class="col-md-8 offset-md-2">
          <h5>1. Install</h5>
          <div>
          Using npm
          <div style="background-color:#c1c1c1;padding:7px; border-radius:0px;">
              <pre><code>npm install --save vue-instant</code></pre>
          </div>
          </div>

        <div style="margin-top:20px;">
          Using bower
          <div style="background-color:#c1c1c1;padding:7px; border-radius:0px;">
              <pre><code>bower install --save vue-instant</code></pre>
          </div>
        </div>

        <div style="margin-top:20px;">
          Using cdn
          <div style="background-color:#c1c1c1; padding:7px; border-radius:0px;">
              <pre><code>&lt;script src="https://unpkg.com/vue-instant/dist/vue-instant.min.js">&lt;/script></code></pre>
          </div>
      </div>

      </div>
  </div>


<div class="row" style="margin-top:40px;">
      <div class="col-md-8 offset-md-2">
          <h5>2. Import the component directly from your Vue component file:</h5>
          <div style="background-color:#c1c1c1; padding:7px; border-radius:0px;">
              <pre><code>// ES6 import { VueTyper } from 'vue-instant'<br>// CommonJS var VueInstant = require('vue-instant').VueInstant <br>// Global var VueInstant = window.VueInstant.VueInstant</code></pre>
          </div>
      </div>
  </div>



  <div class="row" style="margin-top:40px;">
      <div class="col-md-8 offset-md-2">
          <h5>3. Insert in your template</h5>
          <div style="background-color:#c1c1c1; padding:7px; border-radius:0px;">
              <pre>&lt;vueInstant <br> &nbsp;&nbsp;:suggestion-attribute="suggestionAttribute" <br> &nbsp;&nbsp;:suggestions="suggestionsArray" <br> &nbsp;&nbsp;:type="style" <br> &nbsp;&nbsp;v-model="value"&gt; <br>&lt;/vueInstant&gt;</pre>
          </div>
      </div>
  </div>


    <div class="row" style="margin-top:40px;">
      <div class="col-md-8 offset-md-2">

            <h5>4. Test with your favorite film and see the changes in realtime</h5>

            <vueInstant style="background:#202f40; padding:10px;" type="amazon" :suggestion-attribute="suggestionAttribute" placeholder="type your favorite film..." @selected="setInstantData"  @input="changed" :suggestions="suggestions" v-model="val"></vueInstant>

            <p style="margin-top:10px;">
              While you typing the model changes and you can send a request to your server
              and get new data and pass to the component in the suggestions attribute.
              In this example I use a free API to get the films provided by themoviedb.org
            </p>

            <p style="background-color: rgba(255, 255, 0, 0.239216);">
              if you use the autocomplete you can see the results here
            </p>

            <pre>{{instantData}}</pre>

      </div>
    </div>


<div class="row" style="margin-top:40px; margin-bottom:1px;">
    <div class="col-md-8 offset-md-2">
        <h5>5. Attributes</h5>
        <table class="table table-bordered">
          <thead>
            <tr class="bg-inverse" style="color:white;">
              <td>Attribute</td>
              <td>Description</td>
              <td>type</td>
              <td>Required</td>
              <td>Default</td>
              <td>Accepted values</td>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>v-model</td>
              <td>binding the component value.</td>
              <td>String</td>
              <td>true</td>
              <td>-</td>
              <td>-</td>
            </tr>
            <tr>
              <td>suggestions</td>
              <td>collection of suggestions that appear while the user is typing</td>
              <td>Array</td>
              <td>true</td>
              <td>-</td>
              <td>-</td>
            </tr>
            <tr>
              <td>suggestion-attribute</td>
              <td>The attribute name of the object to render in the searchbox</td>
              <td>string</td>
              <td>true</td>
              <td>-</td>
              <td>-</td>
            </tr>
            <tr>
              <td>type</td>
              <td>The style of the input</td>
              <td>String</td>
              <td>false</td>
              <td>facebook</td>
              <td>['google', 'facebook', 'twitter', 'amazon', 'spotify', 'custom']</td>
            </tr>
            <tr>
              <td>show-autocomplete</td>
              <td>Determine if  only show suggests, or suggests with autocomplete.</td>
              <td>Boolean</td>
              <td>true</td>
              <td>full</td>
              <td>-</td>
            </tr>
            <tr>
              <td>placeholder</td>
              <td>same as placeholder in native input</td>
              <td>String</td>
              <td>false</td>
              <td>write something...</td>
              <td>-</td>
            </tr>
            <tr>
              <td>instant</td>
              <td>You can disable the typing placeholder if don´t like it</td>
              <td>Boolean</td>
              <td>false</td>
              <td>true</td>
              <td>-</td>
            </tr>
            <tr>
            <tr>
              <td>name</td>
              <td>same as name in native input  </td>
              <td>String</td>
              <td>false</td>
              <td>vueInstant</td>
              <td>-</td>
            </tr>
            <tr>
              <td>autofocus</td>
              <td>same as autofocus in native input</td>
              <td>Boolean</td>
              <td>true</td>
              <td>false</td>
              <td>-</td>
            </tr>
            <tr>
              <td>disabled</td>
              <td>same as disabled in native input</td>
              <td>Boolean</td>
              <td>false</td>
              <td>false</td>
              <td>-</td>
            </tr>
          </tbody>
        </table>
    </div>
</div>


<div class="row" style="margin-top:40px; margin-bottom:10px;">
    <div class="col-md-8 offset-md-2">
        <h5>6. Events</h5>
        <table class="table">
          <thead>
            <tr class="bg-inverse" style="color:white;">
              <td>Event</td>
              <td>Description</td>
              <td>Event Data</td>
            </tr>
          </thead>
          <tbody>
           <tr>
              <td>@input</td>
              <td>triggers when the value changes</td>
              <td>(value: string)</td>
            </tr>
            <tr>
              <td>@clickInput</td>
              <td>triggers when the text input is clicked</td>
              <td>(event: Event)</td>
            </tr>
            <tr>
              <td>@clickButton</td>
              <td>triggers when the submit button is clicked</td>
              <td>(value: string)</td>
            </tr>
            <tr>
              <td>@selected</td>
              <td>triggers when the selectedObject changes</td>
              <td>(selected: obj)</td>
            </tr>
            <tr>
              <td>@autocomplete-click</td>
              <td>triggers when the element of autocomplete is clicked</td>
              <td>(selected: obj)</td>
            </tr>
            <tr>
              <td>@enter</td>
              <td>triggers when the enter key is pressed</td>
              <td>(selected: obj)</td>
            </tr>
            <tr>
              <td>@keyUp</td>
              <td>triggers when the arrowup key is pressed</td>
              <td>(selected: obj)</td>
            </tr>
            <tr>
              <td>@keyDown</td>
              <td>triggers when the arrowdown key is pressed</td>
              <td>(selected: obj)</td>
            </tr>
            <tr>
              <td>@keyLeft</td>
              <td>triggers when the arrowlef key is pressed</td>
              <td>(selected: obj)</td>
            </tr>
            <tr>
              <td>@clearClicked</td>
              <td>triggers when the clear button is pressed</td>
              <td>-</td>
            </tr>
            <tr>
              <td>@escape</td>
              <td>triggers when the escape key is pressed</td>
              <td>(event: Event)</td>
            </tr>
          </tbody>
        </table>
    </div>
</div>


<div class="row" style="margin-bottom:30px;">
    <div class="col-md-8 offset-md-2">
        <h5>7. Custom Box</h5>

        <p>You can override the styles and customize the searchbox. It's easy.
        <p>1. You need define the <b style="background:rgba(255, 255, 0, 0.24);">type</b> prop with the 'custom' value</p>
        <p>2. The next step is go to <a href="http://shipow.github.io/searchbox/">
        http://shipow.github.io/searchbox/</a> and generate your own style or create your own.</p>
        <p>3. Copy the css generated in your css file.</p>
        <p>4. Create a new css class called .sbx-custom__input-placeholder with the same properties of the class called ".sbx-custom__input".</p>
        <p>5. Add this class in your css with your autocomplete background and your autocomplete color.</p>
        <div style="background:black; color:yellow; padding:10px;">
         <pre style="color:orange">.vue-instant__suggestions li.highlighted__custom {</pre>
            <pre style="color:yellow;padding-left:30px;">background-color: "your decision";</pre>
            <pre style="color:yellow;padding-left:30px;">color: "your decision";</pre>
          <pre style="color:orange;">}</pre><pre style="color:orange;">.vue-instant__suggestions { </pre>
            <pre style="color: #00ec00;padding-left:30px;">margin-top: your decision;</pre><pre style="color:orange;">}</pre>
        </div>
    </div>
</div>



<div class="row" style="margin-bottom:30px;">
      <div class="col-md-8 offset-md-2">
                    <a class="github-button" href="https://github.com/santiblanko/vue-instant" data-count-href="/santiblanko/vue-instant/stargazers" data-count-api="/repos/santiblanko/vue-instant#stargazers_count" data-count-aria-label="# stargazers on GitHub" aria-label="Star santiblanko/vue-instant on GitHub">Star</a>
                    <a class="github-button" href="https://github.com/santiblanko/vue-instant/fork" data-count-href="/santiblanko/vue-instant/network" data-count-api="/repos/santiblanko/vue-instant#forks_count" data-count-aria-label="# forks on GitHub" aria-label="Fork santiblanko/vue-instant on GitHub">Fork</a>
