</div>
</template>

<script>
import axios from 'axios'
import vueInstant from './can.vue'
export default {

  name: 'builder',
  components: {
    vueInstant
  },
  computed: {
    getStyle () {
      return this.styles[this.type].properties
    }
  },
  methods: {
    setInstantData (o) {
      this.instantData = o
    },
    changeType (type) {
      this.type = type
    },
    changed () {
      var that = this
      this.suggestions = []
      axios.get('https://api.themoviedb.org/3/search/movie?api_key=342d3061b70d2747a1e159ae9a7e9a36&query=' + this.val)
        .then(function (response) {
          response.data.results.forEach(function (a) {
            that.suggestions.push(a)
          })
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  },
  data () {
    return {
      suggestionAttribute: 'original_title',
      instantData: null,
      val: '',
      styles: {
        facebook: {
          properties: 'background:#3e5da0; padding:10px;'
        },
        twitter: {
          properties: 'background:#fff; padding:10px;'
        },
        spotify: {
          properties: 'background:black; padding:10px;'
        },
        amazon: {
          properties: 'background:#202f40; padding:10px;'
        },
        google: {
          properties: 'background:white; padding:10px;'
        },
        custom: {
          properties: 'background:white; padding:10px;'
        }
      },
      type: 'google',
      suggestions: []
    }
  }
}
</script>
<style>


  body {
    overflow-x: hidden;
    font-family: 'Source Sans Pro', sans-serif;
    background-color: #F3F4F7;
  }

footer {
    padding: 2rem;
    color: #fff;
    background: #202f40;
    text-align: center;
}

footer a {
  color:#43ff00;
}

footer a:hover {
  color:#43ff00;
}

  .container {
    margin-top:50px;
  }

  .changer {
    cursor:pointer;
    color:blue;
  }

  .changer:hover {
    cursor:pointer;
  }

</style>



